.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-top: 80px;
  display: flex;
}

.photo-card {
  background-color: rgba(255, 255, 255, .4);
  border-radius: 5px;
  transition: box-shadow .35s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
}

.photo-card:hover {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .3);
}

.photo-card a:focus .photo-card__img {
  filter: grayscale(0%);
  transform: scale(1.05);
}

.img-card {
  filter: grayscale(50%);
  object-fit: cover;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more {
  background-color: #0ff;
  border: none;
  border-radius: 2px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more:hover {
  background-color: #70d8d8;
}

